import { IMG_HOST } from "@/config/host"

export function imgFullPath (path) {
  if (!path) {
    return ""
  }
  if (process.env.NODE_ENV === "production") {
    return IMG_HOST + "/" + path
  }
  const os = require("os")
  if (os.type().includes("Windows")) {
    return "@/../../../wushiwan-data/img/" + path
  }
  return "@/../../../wushiwan-data/img/" + path
}
