import { httpPostWithLoading } from "@/util/request"
import { LOGIN_URL, LOGOUT_URL } from "@/config/host"
import store from "@/store"

const user = {
  state: {
    roles: [],
    token: "",
    user: null
  },
  mutations: {
    userLoginSuccess: (state, data) => {
      state.token = data.token
    },
    userLogout: (state) => {
      state.token = ""
      state.roles = []
      state.user = null
    },
    updateUser: (state, sysUser) => {
      state.user = sysUser
    }
  },

  actions: {
    // 登录
    async login ({ commit }, userInfo) {
      const result = await httpPostWithLoading(LOGIN_URL, userInfo)
      if (result.success) {
        commit("userLoginSuccess", result.data)
        commit("updateUser", result.data.sysUser)
      }
      return result
    },
    // 登出
    async logout ({ commit, state }) {
      store.commit("clearProperty")
      await httpPostWithLoading(LOGOUT_URL, null)
      commit("userLogout")
    },
    refresh ({ commit, state }) {
      return new Promise((resolve, reject) => {
        // setLogin(false)
        // commit("SET_LOGIN", false)
        // httpPost(LOGOUT_URL, null, ".body").then(() => {
        //   resolve()
        // }).catch(error => {
        //   reject(error)
        // })
      })
    }
  }
}

export default user
